<template>
  <div>
    <div class="content">
      <van-radio-group v-model="radio">
        <div class="list" v-for="add in address" :key="add.id">
          <div class="item1">
            <div class="addressTxt1">
              <div class="box">
                收货人:
                <span class="custom-title titleson">{{ add.real_name }}</span>
                <span class="custom-title titleson2">{{ add.phone }}</span>
              </div>
              <div class="box1">
                收货地址:&nbsp;&nbsp;
                {{ add.province }}{{ add.city }}{{ add.district
                }}{{ add.detail }}
              </div>
            </div>
          </div>
          <div class="operation">
            <van-radio
              checked-color="#ee0a24"
              icon-size="18px"
              :name="add.id"
              @click.stop="adddefault(add.id)"
              >设为默认</van-radio
            >
            <div class="icon" @click.stop="deldefault(add.id)">
              <span class="iconfont icon-shanchu"></span>
              <span>删除</span>
            </div>
          </div>
        </div>
      </van-radio-group>
      <van-button round class="signout" to="/address" type="info"
        >新加地址</van-button
      >
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      address: [],
      radio: null,
    };
  },
  created() {
    this.$axios.get("/api/address/list?page=1&limit=20").then((d) => {
      this.address = d.data.data;
      for (let i = 0; i <= this.address.length; i++) {
        if (this.address[i].is_default == 1) {
          this.radio = this.address[i].id;
        }
      }
    });
  },
  methods: {
    adddefault(event) {
      this.$axios
        .post("/api/address/default/set", {
          id: event,
        })
        .then((d) => {
          if (d.data.status == 200) {
            this.$toast({
              message: "选择地址成功",
            });
          } else {
            this.$toast(d.data.msg);
          }
        });
    },
    deldefault(event) {
      this.$axios
        .post("/api/address/del", {
          id: event,
        })
        .then((d) => {
          if (d.data.status == 200) {
            this.$toast({
              message: "删除地址成功",
            });
            this.$axios.get("/api/address/list?page=1&limit=20").then((d) => {
              this.address = d.data.data;
              for (let i = 0; i <= this.address.length; i++) {
                if (this.address[i].is_default == 1) {
                  this.radio = this.address[i].id;
                }
              }
            });
          } else {
            this.$toast(d.data.msg);
          }
        });
    },
  },
};
</script>
<style lang="less" scoped>
.content {
  font-size: 14px;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  img {
    width: 207px;
    height: 168px;
  }
  .van-radio-group {
    width: 100%;
    .list {
      width: 100%;
      padding: 10px 15px;
      box-sizing: border-box;
      border-bottom: 1px solid #ddd;
      .item1 {
        width: 100%;
        display: flex;
        align-items: center;
        margin-bottom: 8px;
        padding: 8px 0;
        .addressTxt1 {
          .box {
            font-weight: 700;
            text-align: left;
            .titleson2 {
              margin-left: 8px;
            }
          }
          .box1 {
            margin-top: 5px;
            text-align: left;
          }
          margin-left: 10px;
        }
      }
      .operation {
        padding-left: 8px;
        display: flex;
        justify-content: space-between;
        .van-radio {
          width: 50%;
        }
        .icon {
          span {
            margin: 0 3px;
          }
        }
      }
    }
  }
  .signout {
    width: 345px;
    height: 45px;
    margin: 38px 15px 0 15px;
    background-color: #e93323;
    border: none;
  }
}
</style>